<style scoped>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .el-form {
    width: 40%;
    margin-top: 50px;
    margin-left: 50px;
  }
</style>
<template>
  <div class="wrapper-view">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '' }"><span @click="back">店铺列表</span></el-breadcrumb-item>
      <el-breadcrumb-item>创建店铺</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form ref="form" :model="form" label-width="80px" class="el-form">
      <el-form-item label="店铺名称">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="上传图片">
        <el-upload
          class="avatar-uploader"
          action="http://upload-z2.qiniup.com"
          :show-file-list="false"
          list-type ="picture"
          :data="QiniuData"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload">
          <img v-if="form.logo" :src="form.logo" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="店铺地址">
        <el-input v-model="form.address_info"></el-input>
      </el-form-item>
      <el-form-item label="联系方式">
        <el-input v-model="form.tel"></el-input>
      </el-form-item>
      <el-form-item label="商家微信">
        <el-input v-model="form.wechat"></el-input>
      </el-form-item>
      <el-form-item label="店铺简介">
        <el-input type="textarea" v-model="form.desc"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">确定</el-button>
        <el-button @click="back">返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import {createStore, editStore, getToken} from '../service'

  export default {
    data () {
      return {
        form: {
          name: '',
          logo: '',
          address_info: '',
          tel: '',
          wechat: '',
          desc: '',
          image: ''
        },
        QiniuData: {
          key: '',
          token: ''
        }
      }
    },
    mounted () {
      if (this.params.name) {
        this.form = this.params
      }
      this.getToken()
    },
    methods: {
      async onSubmit () {
        let res
        if (!this.params.name) {
          res = await createStore(this.form)
        } else {
          res = await editStore(this.params.id, this.form)
        }
        if (res) {
          this.$message.success('操作成功!')
          this.$emit('show', false)
        } else {
          this.$message.error('操作失败!')
        }
      },
      handleAvatarSuccess (res, file) {
        this.$set(this.form, 'logo', 'http://lhxq.top/' + res.key)
      },
      beforeAvatarUpload (file) {
        const isJPG = file.type === 'image/jpeg'
        const isLt2M = file.size / 1024 / 1024 < 2

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!')
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!')
        }
        this.QiniuData.key = `${file.name}`
        return isJPG && isLt2M
      },
      back () {
        this.$emit('show', false)
      },
      async getToken () {
        let res = await getToken()
        this.QiniuData.token = res.data.token
      }
    },
    props: ['params']
  }
</script>



