<style scoped>
  .el-form {
    width: 40%;
    margin-top: 50px;
    margin-left: 50px;
  }
</style>
<template>
  <div class="wrapper-view">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '' }"><span @click="back">商品列表</span></el-breadcrumb-item>
      <el-breadcrumb-item>创建商品</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form ref="form" :model="form" label-width="80px" class="el-form">
      <el-form-item label="商品名称">
        <el-input v-model="form.name"></el-input>
      </el-form-item>

      <el-form-item label="商品名称">
        <el-select v-model="form.store" placeholder="请选择">
          <el-option
            v-for="item in List"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="产品价格">
        <el-input v-model="form.price" type="number"></el-input>
      </el-form-item>
      <el-form-item label="规格">
        <el-input v-model="form.unit"></el-input>
      </el-form-item>
      <el-form-item label="上传图片">
        <el-upload
          action="http://upload-z2.qiniup.com"
          list-type="picture-card"
          :on-preview="handlePictureCardPreview"
          :on-remove="handleRemove"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
          :data = "QiniuData"
          :file-list="fileList"
          :on-change="handleChange">
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
        </el-dialog>
      </el-form-item>
      <el-form-item label="产品详情">
        <el-input type="textarea" v-model="form.detail"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">确定</el-button>
        <el-button @click="back">返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import {createGood, getStoresList, editGood, getToken} from '../service'

  export default {
    data () {
      return {
        form: {
          store: '',
          name: '',
          price: '',
          unit: '',
          detail: '',
          images: ''
        },
        List: [],
        QiniuData: {
          key: '',
          token: ''
        },
        dialogVisible: false,
        dialogImageUrl: [],
        fileList: []
      }
    },
    mounted () {
      this.getList()
      this.getToken()
      if (this.params.name) {
        this.form = this.params
        let picArr = this.params.images.split(',')
        if (picArr.length) {
          for (let val of picArr) {
            if (val) {
              this.fileList.push({url: val})
            }
          }
        }
      }
    },
    methods: {
      async onSubmit () {
        this.form.images = ''
        for (let item of this.fileList) {
          this.form.images += (item.url + ',')
        }
        let res
        if (!this.params.name) {
          res = await createGood(this.form)
        } else {
          res = await editGood(this.params.id, this.form)
        }
        if (res) {
          this.$message.success('操作成功!')
          this.$emit('show', false)
        } else {
          this.$message.error('操作失败!')
        }
      },

      async getList () {
        let res = await getStoresList()
        this.List = res.data.results
      },
      handleRemove (file, fileList) {
        console.log(file, fileList)
        this.fileList = fileList
      },
      handleChange (file, fileList) {
        console.log(fileList)
      },
      beforeAvatarUpload (file) {
        // const isJPG = file.type === 'image/jpeg'
        const isJPG = true
        const isLt2M = file.size / 1024 / 1024 < 2

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!')
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!')
        }
        this.QiniuData.key = `${file.name}`
        return isJPG && isLt2M
      },
      handlePictureCardPreview (file) {
        this.dialogVisible = true
      },
      handleAvatarSuccess (res, file) {
        let str = 'http://lhxq.top/' + res.key
        this.fileList.push({url: str})
      },
      back () {
        this.$emit('show', false)
      },
      async getToken () {
        let res = await getToken()
        this.QiniuData.token = res.data.token
      }
    },
    props: ['params']
  }
</script>



